.popup {
  height: 91%;
  width: 94%;
  background: linear-gradient(90deg, red 50%, transparent 50%), 
              linear-gradient(90deg, red 50%, transparent 50%), 
              linear-gradient(0deg, red 50%, transparent 50%), 
              linear-gradient(0deg, red 50%, transparent 50%);
  background-repeat: repeat-x, repeat-x, repeat-y, repeat-y;
  background-size: 8px 4px, 8px 4px, 4px 8px, 4px 8px;
  background-position: 0% 0%, 100% 100%, 0% 100%, 100% 0px;
  border-radius: 5px;
  padding: 14px;
  animation: dash 5s linear infinite;
  background-color: #d79beb;
}

.popupinside {
  height: 93%;
  width: 96%;
  background: linear-gradient(90deg, red 50%, transparent 50%), 
              linear-gradient(90deg, red 50%, transparent 50%), 
              linear-gradient(0deg, red 50%, transparent 50%), 
              linear-gradient(0deg, red 50%, transparent 50%);
  background-repeat: repeat-x, repeat-x, repeat-y, repeat-y;
  background-size: 8px 4px, 8px 4px, 4px 8px, 4px 8px;
  background-position: 0% 0%, 100% 100%, 0% 100%, 100% 0px;
  border-radius: 8px;
  padding: 10px;
  animation: dash2 10s linear infinite;
}

@keyframes dash {
  to {
      background-position: 100% 0%, 0% 100%, 0% 0%, 100% 100%;
  }
}

@keyframes dash2 {
  to {
      background-position: 100% 0%, 0% 100%, 0% 0%, 100% 100%;
  }
}

